@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:wght@100;200;300;400;500;600;700;800;900&display=swap');

* {
    margin: 0;
    padding: 0;
    border: 0;
    line-height: 1.4em;
    vertical-align: baseline;
    text-decoration: none;
    outline: 0;
    box-sizing: border-box;
    background-color: transparent;
    font-family: 'Noto Sans', sans-serif;
}

$background-color-1: #EBF3FA;
$background-color-2: #5FD58C;
$background-color-3: #F5F5F5;
$background-color-4: #283856;
$background-color-5: #FAFAFA;
$background-color-6: #B6BDCD;
$background-color-7: #F1F1F1;
$background-color-8: #CBF1D9;
$background-color-9: #FFFFFF;
$background-color-10: #F8F8F8;
$font-color-1: #455085;
$font-color-2: #43475D;
$font-color-3: #5FD58C;
$font-color-4: #FAFAFA;
$font-color-5: #626060;
$font-color-6: #B6BDCD;
$font-color-7: #58A67E;
$font-color-8: #64C689;
$font-color-9: #535873;
$font-color-10: #9A5353;
$font-color-11: #68AE81;
$border-color-1: #707070;


/* The container */
.input-container {
    display: block;
    position: relative;
    padding-left: 40px;
    margin-bottom: 12px;
    cursor: pointer;
    font-size: 22px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

/* Hide the browser's default checkbox */
.input-container input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

/* Create a custom checkbox */
.checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 26px;
    width: 26px;
    border: 1px solid $border-color-1;
    border-radius: 5px;
}

/* On mouse-over, add a grey background color */
.input-container:hover input~.checkmark {
    background-color: $background-color-3;
}

/* When the checkbox is checked, add a blue background */
.input-container input:checked~.checkmark {
    background-color: $background-color-2;
    border: 1px solid transparent;
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
    content: "";
    position: absolute;
    display: none;
}

/* Show the checkmark when checked */
.input-container input:checked~.checkmark:after {
    display: block;
}

/* Style the checkmark/indicator */
.input-container .checkmark:after {
    left: 9px;
    top: 5px;
    width: 5px;
    height: 10px;
    border: solid $font-color-4;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
}

