            height: 8px;
            width: 17px;
            cursor: pointer;
        }
    }

    .form {
        width: 460px;
        display: flex;
        flex-direction: column;
        align-items: center;

        form {
            display: flex;
            flex-direction: column;
            align-items: center;
            margin-top: 20px;

            .input {
                position: relative;
                margin-top: 30px;
                display: flex;
                justify-content: flex-start;

                img {
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);
                    right: 20px;
                    height: 22px;
                    width: 38px;
                    cursor: pointer;
                }

                label {
                    font-size: 15px;
                    line-height: 20px;
                    font-weight: 400;
                    color: $font-color-2;
                    font-family: 'Noto Sans', sans-serif;

                    a {
                        font-weight: 600;
                        color: $font-color-3;
                    }
