                input[type="text"],
                input[type="password"] {
                    width: 402px;
                    height: 60px;
                    border: $border-color-1 1px solid;
                    border-radius: 10px;
                    padding-left: 20px;
                    font-size: 20px;
                    font-family: 'Noto Sans', sans-serif;
                    font-weight: 400;
                    color: $font-color-2;
                    line-height: 27px;
                    opacity: 1;
                    text-align: left;
                }

                input::placeholder {
                    font-size: 20px;
                    font-family: 'Noto Sans', sans-serif;
                    font-weight: 400;
                    color: $font-color-2;
                    line-height: 27px;
                    opacity: 0.7;
                    text-align: left;
                }

                &.checkbox {
                    padding: 0 20px;
                    margin-top: 40px;
                    align-self: flex-start;
                }
