            border-radius: 10px;
            margin-top: 60px;
            display: flex;
            align-items: center;
            position: relative;
            padding: 0 10px;

            .flag {
                margin: 0 10px;
            }

            .symbol {
                font-size: 18px;
                line-height: 24px;
                color: $font-color-5;
            }

            .arrow {
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                right: 25px;
            }
        }
    }
}

