            .rows {
                display: flex;
                flex-direction: column;


                .row {
                    display: flex;
                    height: 56px;
                    width: 1254px;
                    margin-top: 20px;


                    &.sub {
                        justify-content: flex-end;

                        .data {
                            width: 1026px;

                            .cell {
                                &:nth-child(1) {
                                    width: 291px;
                                }

                                &:nth-child(2) {
