                                }

                                &:nth-child(4) {
                                    width: 183px;
                                }

                                &:nth-child(5) {
                                    width: 67px;
                                }
                            }
                        }
                    }
                }

                .point {
                    width: 94px;
                    display: flex;
                    font-weight: 600;
                    font-family: 'Poppins', sans-serif;
                    align-items: center;
                    background-color: $background-color-10;
                    font-size: 20px;
                    justify-content: center;
                    margin-right: 20px;
                    border-radius: 15px;

                }


                .data {
                    display: flex;
                    width: 1140px;
                    align-items: center;
                    background-color: $background-color-10;
                    border-radius: 15px;
                    position: relative;

                    &.width_1:after {
                        width: calc((1892/4300)*100%);
                    }

                    &.width_2:after {
                        width: calc((1000/2500)*100%);
                    }

                    &.width_3:after {
                        width: calc((892/1800)*100%);
                    }

                    &.width_4:after {
                        width: calc((2600/2600)*100%);
                        border-top-right-radius: 15px;
                        border-bottom-right-radius: 15px;
                    }

                    &.width_5:after {
                        width: calc((906/1200)*100%);
                    }

                    &.width_6:after {
                        width: calc((300/900)*100%);
                    }

                    &.width_7:after {
                        width: calc((500/2000)*100%);
                    }

                    &.width_8:after {
                        width: calc((500/2000)*100%);
                    }

                    &.width_9:after {
                        width: calc((500/1000)*100%);
                    }

                    &:after {
                        content: "";
                        position: absolute;
                        left: 0;
                        background-color: $background-color-1;
                        height: 100%;
                        z-index: 0;
                        border-top-left-radius: 15px;
                        border-bottom-left-radius: 15px;
                    }

                    .cell {
                        font-size: 20px;
                        color: $font-color-2;
                        z-index: 1;

                        &:nth-child(1) {
                            width: 405px;
                            font-weight: 500;
                            padding-left: 20px;
                        }

                        &:nth-child(2) {
                            width: 240px;
                        }

                        &:nth-child(3) {
                            width: 245px;
                        }

                        &:nth-child(4) {
                            width: 183px;
                        }

                        &:nth-child(5) {
                            width: 67px;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            cursor: pointer;
                        }
                    }
                }
            }
        }

        .account-summary {
            width: 1364px;
            margin: 40px auto 100px auto;
            background-color: $background-color-9;
            border-radius: 15px;
            box-shadow: 0 3px 6px rgba(0, 0, 0, 16%);
            padding-top: 30px;
            padding-left: 30px;
            padding-bottom: 40px;

            .boxes {
                display: flex;
                margin-top: 40px;
                font-size: 23px;
                font-weight: 600;
                color: $font-color-9;



                .box {
                    width: 25%;
                    display: flex;
                    border-right: 1px solid rgba($border-color-1, 23%);
                    padding: 15px 0 15px 30px;


                    &:first-child {
                        padding: 15px 0 15px 0;
                    }

                    &:last-child {
                        border-right: none;
                    }

                    .image {
                        display: flex;
                        align-items: flex-start;

                        img[alt="account-type"] {
                            height: 30px;
                        }
                    }

                    .wrapper {
                        margin-left: 20px;
                        display: flex;
                        flex-direction: column;

                        .info-title {
                            font-family: 'Poppins', sans-serif;
                        }

                        .info-data {
                            font-weight: 400;
                            margin-top: 11px;
                            font-family: 'Poppins', sans-serif;
                        }
                    }
                }
            }
        }

        .account-history {
            width: 1364px;
            margin: 40px auto 100px auto;
            background-color: $background-color-9;
            border-radius: 15px;
            box-shadow: 0 3px 6px rgba(0, 0, 0, 16%);
            padding-top: 30px;
            padding-left: 30px;
            padding-bottom: 40px;
            padding-right: 30px;

            .headers {
                display: flex;
                margin-top: 38px;
                height: 85px;
                background-color: $background-color-1;
                border-radius: 15px;
                align-items: center;
                margin-left: -15px;
                margin-right: -15px;
                width: calc(100% + 30px);

                .header {

                    font-size: 20px;
                    font-weight: 500;
                    color: $font-color-2;

                    &:nth-child(1) {
                        width: 13%;
                    }

                    &:nth-child(2) {
                        width: 15%;
                    }

                    &:nth-child(3) {
                        width: 18%;
                    }

                    &:nth-child(4) {
                        width: 18%;
                    }

                    &:nth-child(5) {
                        width: 18%;
                    }

                    &:nth-child(6) {
                        width: 18%;
                    }

                }
            }

            .date-bar {
                width: calc(100% + 60px);
                background-color: $background-color-1;
                height: 34px;
                display: flex;
                align-items: center;
                margin-top: 28px;
                margin-left: -30px;
                margin-right: -30px;
                padding-left: 30px;
                font-size: 18px;
                color: $font-color-2;
            }

            .transaction-row {
                height: 122px;
                display: flex;
                margin-left: -15px;
                margin-right: -15px;
                width: calc(100% + 30px);
                align-items: center;

                .trans-col {
                    font-size: 20px;
                    color: $font-color-2;

                    &:nth-child(1) {
                        width: 13%;
                        padding-left: 15px;
                    }

                    &:nth-child(2) {
                        width: 15%;
                        font-weight: 500;

                        &.receipts {
                            color: $font-color-11;
                        }

                        &.expenses {
                            color: $font-color-10;
                        }

                    }

                    &:nth-child(3) {
                        width: 18%;
                    }

                    &:nth-child(4) {
                        width: 18%;
                    }

                    &:nth-child(5) {
                        width: 18%;
                    }

                    &:nth-child(6) {
                        width: 18%;
                    }
                }
            }

            hr {
                border-top: 1px solid rgba($border-color-1, 20%);
                margin-left: -30px;
                margin-right: -30px;
                width: calc(100% + 60px);
            }

            .see-more {
                display: flex;
                justify-content: center;
                align-items: center;
                margin-top: 30px;
                font-size: 20px;
                color: $font-color-8;
                cursor: pointer;

                img {
                    margin-left: 10px;
                }
            }
        }
    }
}